<template>
<section>
    <loader :is-visible="isLoading"></loader>
    <div id="modelAddProduct" role="dialog" class="modal my-popups right fade" v-cloak>

        <div role="document" class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header mheader-user">
                    <button type="button" class="close" v-on:click="close()">
                        <em aria-hidden="true" class="fa fa-times"></em>
                    </button>
                    <h4 id="myModalLabel" class="modal-title">{{ $t("AddItem") }}</h4>
                    <span class="user-guide">
                        <a class="ancuserguide" data-toggle="collapse" href="#collapseReplyUG" role="button" aria-expanded="false" aria-controls="collapseExample"> User Guide</a>
                        <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                            <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                <div class="con" v-html="$t('UserGuideAddProduct')"></div>
                            </div>
                        </div>
                    </span>
                </div>
                <div class="modal-body">
                    <div class="left-menu-tab new_tab" v-if="popupCondition == 'setup'">
                        <div class="col-md-12 pb-0">
                            <div class="row">
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                                    <div class="row">
                                        <div class="col-8 pr-0 font-weight-medium">{{ $t('CompleteYourProfile')}}</div>
                                        <div class="col-4 pl-0 text-right">
                                            <small>
                                                <i class="fa fa-check-circle pr-1" v-bind:class="{ 'text-success': SetupProgresText == 'Completed', 'text-muted': SetupProgresText !== 'Completed' }"></i>
                                                {{SetupProgresText }}
                                           </small>
                                        </div>
                                    </div>
                                    <div class="progress smaller my-2">
                                        <div s class="progress-bar progress-bar-primary progress-bar px-0 text-center memoryused" id="divConsumendStorage" :class="`${progressBarColor}`" :style="`width: ${SetupProgress}%; font-weight:500; font-size:14px`" :title="SetupProgress">{{ SetupProgress }}%
                                        </div>
                                    </div>
                                    <div class="level_info my-2 d-block float-left w-100" style="height:auto;">
                                        <ul>
                                            <li>
                                                <b id="divConsumendStorage"><span>{{$t('Progress')}} {{ CurrentTab }} of 3</span></b>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="mt-0" id="divscrollsetuptabs" style="max-height: calc(100vh - 2px);">
                            <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix newtab_des">
                                <li class="nav-item FourthScreen" @click="SkipTab(1)"><a class="nav-link left-menu" :class="{ 'active': step == 1 }" style="cursor: pointer;"><i class="fa fa-random pr-2 text-muted" aria-hidden="true"></i><span class="font-14">{{ $t('SetUpLocation') }}</span></a></li>
                                <li class="nav-item SecondScreen" @click="SkipTab(2)"><a class="nav-link left-menu" :class="{ 'active': step == 2 }" style="cursor: pointer;"><i class="fa fa fa-cog pr-2 text-muted" aria-hidden="true"></i><span class="font-14">{{ $t('SetUpProductType') }}</span></a>
                                </li>
                                <li class="nav-item ThirdScreen" @click="SkipTab(3)"><a class="nav-link left-menu" :class="{ 'active': step == 3 }" style="cursor: pointer;"><i class="fa fa-plus pr-2 text-muted" aria-hidden="true"></i><i class="fa fa-check-circle font-18 pr-2 text-success" v-if="IsClientStoragesCopmleted" aria-hidden="true"></i><span class="font-14">{{ $t('AddDeployeItem') }}</span></a>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div class="border p-3" v-if="step == 3">
                        <!-- {{ FormSchema[0].Data[3] }} -->
                        <Form ref="AddProduct">
                            <dynamic-form ref="AddProductForm" :buttons="buttons" :schema="FormSchema">
                                <!-- v-on:OnSubmit="onSubmit" -->
                           
                                <template v-slot:tgslot-unit_price="{ data }">  
                                    <Field :rules="{ 'required': true,'max': 5 }" class="w-100"  name="Unit Price" v-slot="{ errors }">                                 
                                        <div class="input-group">
                                            <span class="input-group-append">
                                                <span class="input-group-text" id="spnOutOF">{{ Currency }}</span>
                                            </span>
                                            <input type="number" name="unitprice" min="0" :maxlength="5" step="any" class="form-control no-spinner" v-on:input="limitLength" v-on:keyup="GetAssetCost($event,data.fieldInfo.value)" pattern="\d+\.?\d?(?!\d)" v-model="data.fieldInfo.value">
                                 
                                        </div>
                                        <span v-if="errors[0]" name="unitprice" class="invalid-feedback d-block">{{errors[0]}}</span>
                                    </Field>
                               
                                </template>
                                <template v-slot:tgslot-asset_cost="{ data }">

                                    <div class="input-group">
                                        <span class="input-group-append">
                                            <span class="input-group-text" id="spnOutOF">{{ Currency }}</span>
                                        </span>
                                        <input type="number" :min="0" class="form-control no-spinner" v-on:keyup="GetUnitPrice(data.fieldInfo.value)" v-model="data.fieldInfo.value">

                                    </div>
                                </template>
                                <template v-slot:tgslot-location_id="{ data }">
                                    <div class="input-group">
                                        <Field name="Location" :rules="{ 'required': true }" v-slot="{ errors }">
                                            <tree-view v-model="TreeValue" :options="options" placeholder="Select Location" :show-count="true" v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }">
                                            </tree-view>
                                            <span v-if="errors[0]" name="Location" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                        </Field>
                                    </div>
                                </template>
                            </dynamic-form>
                        </Form>
                    </div>
                    <div class="right-content row flex-row-reverse mt-3" v-if="step == 3">
                        <div class="col-lg-12 text-left">
                            <div class="py-2 mandatory">
                                {{ $t('MandatoryString') }}
                            </div>
                        </div>
                    </div>

                    <div class="right-content">
                        <div class="col-md-12 p-0" v-if="popupCondition == 'setup'">
                            <div class="col-md-12 p-0" v-if="step == 1">
                                <div class="tab-content">
                                    <div id="step1" class="tab-pane active show">
                                        <section class="page-action w-100">
                                            <div class="container-fluid p-0">
                                                <div class="col-md-12 p-0s addpopuplocation">
                                                    <AddLocationPopup :popupCondition="popupCondition">
                                                    </AddLocationPopup>
                                                </div>
                                            </div>
                                        </section>
                                    </div>
                                </div>
                            </div>
                            <div class="col-md-12 p-0" v-if="step == 2">
                                <div class="tab-content">
                                    <div id="step2" class="tab-pane active show">
                                        <section class="page-action w-100">
                                            <div class="container-fluid p-0">
                                                <div class="col-md-12 p-0">
                                                    <AddProductType :popupCondition="popupCondition"></AddProductType>
                                                </div>
                                            </div>
                                        </section>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>
    <div class="my-popups" v-if="ShowAddLocation">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('Location') }}
                        </h5>
                        <button v-on:click="CloseLocationPopup()" type="button" class="close" data-original-title="Close"><em class="fa fa-times"></em></button>

                    </div>
                    <AddLocationPopup></AddLocationPopup>
                </div>
            </div>
        </div>
    </div>

    <TagNamePopup v-if="IsTagNamePopup" :FromAddProduct="FromAddProduct"></TagNamePopup>
    <div class="my-popups" v-if="ShowAddProductType">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('ProductType') }}
                        </h5>
                        <button v-on:click="ClosePopupProductType()" type="button" class="close" data-original-title="Close"><em class="fa fa-times"></em></button>
                    </div>
                    <AddProductType :FromPage="FromPage"></AddProductType>
                </div>
            </div>
        </div>
    </div>
</section>
</template>

<script>
import DataService from '../../service/DataService'
import TagNamePopup from '../Modal/TagNamePopup.vue';
import AddLocationPopup from '../Modal/AddLocation.vue'
import AddProductType from '../Modal/AddProductType.vue'
import Treeselect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
export default {
    props: {
        popupCondition: {
            type: String,
            required: false
        },
        FromPage: {
            type: String,
            required: false
        }
    },
    components: {
        TagNamePopup,
        AddLocationPopup,
        AddProductType,
        'tree-view': Treeselect
    },
    data() {
        return {
            options: [],
            TreeValue: null,
            isLoading: false,
            AssetType: [],
            BarcodeType: [],
            userLoggedIn2: false,
            IsDeployment: false,
            IsTagNamePopup: false,
            LocationData: [],
            ShowAddLocation: false,
            ShowAddProductType: false,
            FromAddProduct: false,
            DeploymentType: '',
            IsEnableAssetManagement: false,
            fileUploaded: '',
            tagData: [],
            isCheckDeployItem: '',
            DeployProductValue: false,
            StaticObj: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TreeSelectField",
                            label: this.$t('Location'),
                            name: "Location",
                            value: null,
                            placeholder: "Select Location",
                            config: {
                                options: [],
                                showAddIcon: true,
                                onAddButtonClick: this.AddLocation
                            },
                            validationRules: {
                                "required": true
                            },
                        },
                    ]
                },                
            ],
            FormSchema: [], 
            buttons: [{

                    type: "submit",
                    class: "btn btn-secondary",
                    text: "<i class='fa fa-angle-double-left pr-2'></i>Back",
                    onClick: this.BackButton,
                    isVisible: false,
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save And Close",
                    name: "SaveAndClose",
                    onClick: this.onSubmit

                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save And New",
                    name: "SaveAndNew",                    
                    onClick: this.onSubmit

                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.close,
                    isVisible: true,

                },
                {
                    type: "button",
                    class: "btn btn-success",
                    text: "<i class='fa fa-check' aria-hidden='true'></i> Finish",
                    isVisible: false,
                }
            ],
            DataLoaded: 0,
            TagsArray: [],
            tags: [],
            file: [],
            CurrentTab: 1,
            step: 3,
            SetupProgress: 0,
            count: 0,
            PageSize: 10,
            PageNumber: 1,
            SortBy: '',
            SortExp: '',
            IsClientStoragesCopmleted: false,
            UserType: '',
            FromPage: 'AddProduct',
            fileMulitpleData: [],
            AllowedFileType: [],
            currency:''
        }
    },
    watch: {

        CurrentTab: {
            handler(newValue, oldValue) {
                if (newValue==3) {
                    this.getForm();
                }
            },           
        }
        
    },
    async created() {
        ////debugger;
        var vm = this;
        await vm.EnableAssetManagement();
        vm.Currency = this.GetUserInfo.Currency;
        debugger;
        await vm.getForm();       
        await vm.GetLocationByCompanyId();              
        vm.DataLoaded = 1;

    },
    mounted() {
        $('#modelAddProduct').modal({
            backdrop: 'static',
            keyboard: false
        })
    },    
    methods: {     
        
        getForm: async function (id) {
            ////debugger
            var vm = this;
            vm.isLoading = true;

            var url = `recordId=0&moduleName=Asset&subModuleCode=MANAGE_PRODUCT&usefor=Add`;
            await DataService.GetDynamicForm(url).then(function (response) {
                ////debugger
                if (response != null && response.data != null) {
                    ////debugger;
                    //ths.isMapFeature = response.data.isFsmLocating;
                    if (typeof(response.data.configdata)!='undefined' && response.data.configdata.length >0) {
                        var configResult = response.data.configdata;
                        configResult.forEach(function (row) {
                            if (row.CORE_CONFIG_KEY == "TicketDisallowedFileTypes") {
                                vm.AllowedFileType = row.CONFIG_DATA_VALUE;

                            }
                        });
                    }
                    var assetData  =response.data.data;
                    vm.FormSchema = vm.$refs.AddProductForm.ConvertCustomFieldIntoDynamicFormObj(response.data.data);

                    ////debugger;
                    assetData.forEach(item=>{
                    vm.FormSchema.forEach((group, gi) => {

                        group.Data.forEach((field, fi) => {
                                                        ////debugger;

                            if (group.group_name == "DEPLOY PRODUCT" || group.group_name == "DEPLOY PRODUCT DETAILS") {
                                group.visibility = false;
                                if (field.name == "deployement_type" || field.name == "location_id" || field.name == "quantity" || field.name == "unit_price" || field.name == "asset_cost" || field.name == "status_id") {
                                    field.visibility = false;
                                }
                            }
                            if (field.name == "quantity")
                            { 
                                if(item.name=='quantity'){
                                    field.validationRules= {'required':item.is_required, max: item.length}
                                }
                            }
                            if(field.name == "unit_price") {
                                field.astype = 'SlotField';
                                field.is_required = false; 
                                if(item.name=='unit_price'){
                                    field.validationRules= {'required':item.is_required, max: item.length}
                                }
                            }                          
                            if(field.name == "asset_cost") {
                                field.astype = 'SlotField';                                
                                //field.validationRules= {'required':item.is_required, max: item.length}         

                                //field.validationRules= "max:5"
                            }
                            if (field.name == "location_id") {                               
                                field.astype = 'SlotField';
                                field.is_required = false;
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.AddLocation;                               
                            }

                            if (field.name == "deployement_type") {
                                ////debugger;
                                field.value = "1";
                                //field.display_order = 2;
                            }
                            if (field.name == "type") {
                                ////debugger;
                                field.value = false;
                                //field.display_order = 1
                            }

                            if (field.name == "create_unique_name_list") {
                                field.value = "0";
                                //field.required
                            }
                            if (field.name == "asset_type_id") {
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.OpenAddProductTypePopup;
                            }
                            if (field.name == "tag_id") {
                                field.mode = "tag"
                                field.is_required = false;
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = vm.OpenAddTagPopup;
                            }
                            if (field.name == "type") {
                                field.value = '0'
                                field.is_required = true;
                            }
                            if (field.name == "status_id") {
                                field.value = '1001'
                                field.visibility = false;
                            }

                            else if (field.name == "attachment_path") {
                                field.showLabel = false;
                                let fileTypeList = vm.AttachmentDisallowedFileType(vm.AllowedFileType);
                                if (fileTypeList != null) {
                                    field.config =
                                    {
                                        maxSize: 5,
                                        //accept: fileTypeList,
                                        //onChange: ths.GetBase64String,
                                        accept: [{
                                            fileType: 'jpg', // Image types
                                            iconClass: 'fa-file-image-o' // Icon class for images
                                        },
                                        {
                                            fileType: 'jpeg', // Image types
                                            iconClass: 'fa-file-image-o' // Icon class for images
                                        },
                                        {
                                            fileType: 'png', // Image types
                                            iconClass: 'fa-file-image-o' // Icon class for images
                                        },
                                        // Add more image, document, text, and video types as needed
                                    ],
                                        showAddIcon: false,
                                        onAddButtonClick: '',
                                        multiple: true
                                    }
                               }

                            }
                            // if (field.name == "attachment_path") {
                            //     field.showLabel = false;
                            //     field.config = {
                            //         maxSize: 5,
                            //         accept: [{
                            //                 fileType: 'jpg', // Image types
                            //                 iconClass: 'fa-file-image-o' // Icon class for images
                            //             },
                            //             {
                            //                 fileType: 'jpeg', // Image types
                            //                 iconClass: 'fa-file-image-o' // Icon class for images
                            //             },
                            //             {
                            //                 fileType: 'png', // Image types
                            //                 iconClass: 'fa-file-image-o' // Icon class for images
                            //             },
                            //             // Add more image, document, text, and video types as needed
                            //         ],
                            //         //onChange: ths.GetBase64String,
                            //         showAddIcon: false,
                            //         onAddButtonClick: '',
                            //         multiple: false
                            //     }
                            // }
                            field.config.onChange = vm.onChangeEventForFields;
                        });
                    });
                })
                    // ths.FormSchema.push({
                    //     layoutType: "single",
                    //     group_name: 'Address',
                    //     visibility: false,
                    //     Data: [{
                    //         astype: "SlotField",
                    //         name: "addresFields",
                    //         value: "",
                    //         visibility: false,
                    //         showLabel: false
                    //     }]
                    // });

                    vm.isLoading = false;
                }
            });
        },
        onChangeEventForFields: function (x, y, z) {
            ////debugger
            let vm = this;
            if (y.name == "type") {
                vm.FormSchema.forEach((group, gi) => {
                    ////debugger;
                    group.Data.forEach((field, fi) => {
                        ////debugger;
                        if (y.value) {
                            if (group.group_name == "DEPLOY PRODUCT" || group.group_name == "DEPLOY PRODUCT DETAILS") {
                                group.visibility = true;
                                if (field.name == "deployement_type" || field.name == "location_id" || field.name == "quantity" || field.name == "unit_price" || field.name == "asset_cost") {
                                    field.visibility = true;
                                }
                            }
                        } else {
                            if (group.group_name == "DEPLOY PRODUCT" || group.group_name == "DEPLOY PRODUCT DETAILS") {
                                if (group.group_name == "DEPLOY PRODUCT DETAILS") {
                                    group.visibility = false;
                                }
                                if (field.name == "deployement_type" || field.name == "location_id" || field.name == "quantity" || field.name == "unit_price" || field.name == "asset_cost") {
                                    field.visibility = false;
                                }
                            }
                        }
                    });
                });
            }
            if (y.name == "attachment_path") {
                    vm.GetBase64String(y.value);
            } 
            var Quantity= parseFloat(vm.FormSchema[2].Data[1].value =="" ? 0:vm.FormSchema[2].Data[1].value);
            var unitPrice= parseFloat(vm.FormSchema[2].Data[2].value=="" ? 0:vm.FormSchema[2].Data[2].value);
            //var assetCost= vm.FormSchema[2].Data[3].value;
            if (y.name == "quantity" ) {
                ////debugger;
                if (parseFloat(y.value) > 0 && unitPrice >0) {
                    var assetCost = parseFloat(y.value) * parseFloat(unitPrice);
                    vm.FormSchema[2].Data[3].value = assetCost.toFixed(3);
                } else {
                    vm.FormSchema[2].Data[3].value = "0.000";
                }
            }
            // if (y.name == "unit_price") {
            //     ////debugger;
            //     if (parseFloat(y.value) > 0 && Quantity>0 ) {
            //         ////debugger;
            //         var assetCost = parseFloat(y.value) * parseFloat(Quantity);
            //         vm.FormSchema[2].Data[3].value = assetCost.toFixed(3);
            //     } else {
            //         vm.FormSchema[2].Data[3].value = "0.000";
            //     }
            // } 
            // if (y.name == "asset_cost" ) {
            //     ////debugger;
            //     if (parseFloat(y.value) > 0 &&  Quantity>0 && unitPrice > 0) {
            //         var assetCost = parseFloat(unitPrice) * parseFloat(Quantity);
            //         vm.FormSchema[2].Data[3].value = assetCost.toFixed(3);
            //     } else {
            //         vm.FormSchema[2].Data[3].value = "0.000";
            //     }
            // } 
                         
        },
        GetBase64String: function (evt) {
            try {
                var vm = this;
                var f = evt[0]; // FileList object
                // var f = evt.target.files[0]; // FileList object
                var reader = new FileReader();
                let fileObj = {};
                // Closure to capture the file information.
                reader.onload = (function (theFile) {
                    return function (e) {
                        var binaryData = e.target.result;
                        //Converting Binary Data to base 64
                        var base64StringEncoded = window.btoa(binaryData);
                        //showing file converted to base64
                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);
                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;

                            fileObj.attachement_mime = attachemnetMime,
                            fileObj.fileName = fileName,
                            fileObj.base64String = base64StringEncoded,
                            fileObj.fileSize = f.size,
                            fileObj.contentType = f.type,
                            fileObj.ModuleName = 'Asset',
                            fileObj.SubModuleCode = 'Manage_Products'
                            vm.fileMulitpleData.push(fileObj);
                    };
                })(f);
                // Read in the image file as a data URL.
                reader.readAsBinaryString(f);

            }
            catch (ex) {
            }
        },
        OnDeplymentType(event, field) {
            field.value = event.target.getAttribute('data-value');
            console.log(event.target.getAttribute('data-value'))
        },
        async AssetCatalogueListing() {
            var vm = this;
            vm.isLoading = true;
            var prams = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&${vm.searchCondition}`;
            await DataService.AssetCatalogueListing(prams).then(response => {
                if (response.data.length > 0) {
                    vm.count = vm.count + 1;
                    vm.SetupProgresText = 'Completed';
                } else {
                    vm.SetupProgress = 0;
                }
                vm.isLoading = false;
            })
        },
        GetProgress() {
            var vm = this;
            if (vm.count == 1) {
                vm.SetupProgress = 33.33;
            } else if (vm.count == 2) {
                vm.SetupProgress = 66.66;
            } else if (vm.count == 3) {
                vm.SetupProgress = 100;
            }
        },
        BackButton() {
            var vm = this;
            vm.SkipTab(2);
        },
        SkipTab: function (s) {
            var vm = this;
            vm.step = s;
            vm.CurrentTab = s;
        },
        AdvancedSetupFields() {
            var vm = this;
            if (vm.popupCondition == 'setup') {
                vm.FormSchema[0].Data[5].visibility = false;
                vm.FormSchema[2].Data[0].visibility = false;
                vm.step = 1;
                vm.buttons[1].isVisible = false;
                vm.buttons[0].isVisible = true;
                vm.buttons[3].isVisible = false;
                vm.buttons[4].isVisible = true;
            }
        },
        async GetTagsByModuleI() {

            var vm = this;
            await DataService.GetTagsByModuleId('moduleName=Asset').then(function (response) {
                vm.TagsArray = JSON.parse(response.data).data;
                vm.TagsArray.forEach((itm, index) => {
                    vm.FormSchema[0].Data[5].config.options.push({
